export default function (s) {
    s.state = { }
    s.id = { }
    s.audioFile = { }
    s.canvasWidth = { }
    s.canvasHeight = { }
    s.dispatch = () => { }
    let strokeVal = 50
    let highlighted = false
    let currentID = null
    let offset = 0
    let numLines = 11
    let sW = 4
    const hoverSketch = () => {
	if (s.mouseX > 0 && s.mouseX < s.width && s.mouseY > 0 && s.mouseY < s.height) {
	    if (highlighted === false) {
		strokeVal = 150
		highlighted = true
		/* console.log('moused over: ' + s.id) */
		s.mySound.setVolume(0.9, 0.01)
		s.mySound.play()
	    }
	} else {
	    strokeVal = 50
	    offset = 1.1
	    s.mySound.setVolume(0, 0.1)
	    s.mySound.pause(0.11)
	    highlighted = false
	}
	s.dispatch({ type: 'SET_P5_STROKE' }, { type: 'TOGGLE_PLAY_AUDIO' })
    }
    s.preload = () => {
        s.soundFormats('mp3', 'ogg')
        s.mySound = s.loadSound(s.audioFile)
    }
    s.setup = () => {
        s.createCanvas(s.canvasWidth, s.canvasHeight)
        // console.log('::: displayDensity:', s.displayDensity())
        // console.log('::: pixelDensity:', s.pixelDensity())
	currentID = s.id
	/* console.log('::: sketch-1 has been initialized with ID:' + s.id) */
	s.mySound.setLoop(true)
	s.mySound.setVolume(0)
    }

    s.draw = () => {
	s.background(10, 25, 48)
	if (s.id === currentID) {
	    hoverSketch()
	}
	s.strokeWeight(4)
	s.stroke(255, 255, 255, strokeVal)
	for (let i = 1; i < numLines; i++) {
	    const x = ((s.deltaTime * offset) + (i * (s.width / (numLines - 1)))) % s.width
	    s.line(x, 0, x, s.height)
	}
	if (highlighted === true) {
	    offset = s.map(s.mouseX, 0, s.width, 20, 3)
	    
	    numLines = s.map(s.mouseX, 0, s.width, 4, 20)
	    numLines = s.constrain(numLines, 4, 20)
	    
	    sW = s.map(s.mouseX, 0, s.width, 4, 0.1)
	    sW = s.constrain(sW, 0.01, 4)
	} else {
	    numLines = 11
	    sW = 4
	}
	let speed = s.map(s.mouseX, 0.1, s.width, 0, 8)
	speed = s.constrain(speed, 0.1, 8)
	s.mySound.rate(speed)
    }
}
